@import '../utils.sass';
@import '~bulma/sass/elements/icon.sass';

@font-face {
  font-family: 'react-bulma-componentsregular';
  src: url('data:font/eot;base64,') format('embedded-opentype'), url('data:font/ttf;base64,') format('truetype'), url('data:font/woff;base64,') format('woff'), url('data:font/svg;base64,') format('svg');
  font-weight: normal;
  font-style: normal; }

[class^="rbc-"], [class*=" rbc-"] {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: 'react-bulma-componentsregular' !important;
  speak: none;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;

  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

.rbc {
  &.rbc-bars:before {
    content: "\f0c9"; }
  &.rbc-angle-down:before {
    content: "\f107"; }
  &.rbc-upload:before {
    content: "\f093"; } }
